class Index
{
    constructor()
    {

    }
    
    MainGameClosure()
    {
        MaxApi.init({
            "appId": "vn.momo.web.momoacademy",
            "name": "vn.momo.web.momoacademy",
            "displayName": "MoMo Academy",
            "client": {
            "web": {
                "hostId": "vn.momo.web.momoacademy",
                "accessToken": "U2FsdGVkX194ZqKMPmzXS+UkePODhT0lCbCr6KttGeGWgmzKH6Y/QI4g2GeaQbKh+Qz3rW6MO5Z7GLxBKlYBPogGDZ2/QRQWueWL5AdGbOM="
            }
            },
            "configuration_version": 1
        });
        (function(){
            window.addEventListener('unityInited', function(data){
                data.detail.unityInstance.SendMessage('JavaHook', 'SetUserId',10);
                // console.log(data.detail.unityInstance);
                // MaxApiUtils.getAvatar();
                // data.detail.unityInstance.SendMessage('Loader', 'CallEventTest');
                // MaxApi.getProfile((res) => {
                //     // alert(`MaxApi userId: ${res.userId}`);
                //     data.detail.unityInstance.SendMessage('JavaHook', 'SetUserId',res.name);
                //     MaxApi.getAvatarEndPoint((response) => {
                //         // alert(`MaxApi userAvatar: ${response}$`)
                //         data.detail.unityInstance.SendMessage('JavaHook', 'SetAvatar',response);
                //     })
                // });
            });

            window.addEventListener('UpdateInfoEvent',function (){
                MaxApi.getProfile((res) => {
                    // alert(`MaxApi userId: ${res.userId}`);
                    window.unityInstance.SendMessage('JavaHook', 'SetUserId',res.name);
                    MaxApi.getAvatarEndPoint((response) => {
                         //alert(`MaxApi userAvatar: ${response}$`)
                       window.unityInstance.SendMessage('JavaHook', 'SetAvatar',response+'$');
                    })
                });
            });
            window.addEventListener('OnEventTest', function(){
                console.log('test received');
            });
            window.addEventListener('ExitGame', function(){
                console.log('exit game');
                MaxApi.dismiss();
            });
            window.addEventListener("message", function (data) {
                console.log(">>>" + data);
                if(data && data.data && data.data=='backPress'){
                    window.unityInstance.SendMessage('JavaHook', 'BackPress');
                }           
            });
            window.addEventListener('ShareFacebook', function(){
                MaxApi.shareFacebook({link:''},(res)=>{
                    // console.log(res);
                    MaxApi.trackEvent('flappy_pig',{
                        action: 'result_tap_share_channel',
                        channel_detail:'SHARE_FB'
                    });
                });
            });
            window.addEventListener('CopyLink', function(){
                //document.getElementById('fbshare').style.display = 'none';
                MaxApi.copyToClipboard(window.deeplink, 'Đã sao chép');
                MaxApi.trackEvent('flappy_pig',{
                    action: 'result_tap_share_channel',
                    channel_detail:'SHARE_DOWNLOAD'
                });
            });
            window.addEventListener('FBInstance', function(){
                const _url = `fb-messenger://share/?link=${''}`;
                // window.open(_url);
                MaxApi.openURL(_url);
                MaxApi.trackEvent('flappy_pig',{
                    action: 'result_tap_share_channel',
                    channel_detail:'SHARE_MESSENGER'
                });
            });
            //--
            window.addEventListener('SaveImage', function(){
                MaxApi.requestPermission('storage', status => {
                    if (status === 'granted') {
                        MaxApi.saveImage('', result => {
                            if (result) {
                                MaxApi.showToast({
                                    duration: 5000,
                                    title: 'Lưu hình ảnh thành công',
                                });
                                MaxApi.trackEvent('flappy_pig',{
                                    action: 'result_tap_share_channel',
                                    channel_detail:'SHARE_DOWNLOAD'
                                });    
                            } else {
                                MaxApi.showToast({
                                    duration: 5000,
                                    title: 'Lưu hình ảnh không thành công',
                                });
                            }
                        });
                    } else {
                        MaxApi.showToast({
                            duration: 5000,
                            title: 'Lưu hình ảnh không thành công',
                        });
                    }
                });
            });
            //--
            window.addEventListener('MoreMenu', function(){
                let shareOptions = {
                    title: 'Chia sẻ',
                    message: '',
                    url: '',
                    subject: 'Nào mình cùng MoMo nhé!',
                };
                MaxApi.share(shareOptions, result => {});
            });
        }());

        // MaxApi.getProfile((res) => {
        //     const a = JSON.stringify(res);
        //     alert(`MaxApi userId: ${a}`);
        //     MaxApi.getAvatarEndPoint((response) => {
        //         alert(`MaxApi userAvatar: ${response}$`)
        //     })
        // });
    }

    OnReady()
    {
        this.MainGameClosure();
    }
}

export default Index;